<template>

      <!-- <ul id="dots">
        <li v-for="dot in dots">
            <div class="dot"></div>
        </li>
      </ul> -->
    <div id="Select" v-bind:style="{backgroundColor:questionArray[progress-1].bgColor}" @created='setValue'>
        <p id="question" v-bind:style="{color:questionArray[progress-1].textColor}">{{questionArray[progress-1].question}}</p>
        <div class="section">
        <transition name="bounce">
            <img id="picA" @click="next(),countA()" v-bind:src="questionArray[progress-1].picAurl" alt="">
        </transition>
        </div>
        <div class="section">
            <img id="picB" @click="next(),countB()" v-bind:src="questionArray[progress-1].picBurl" alt="">
        </div>
        <p id="progress">{{progress}}/8</p>
    </div>
    

</template>

<script>
export default {
  name: 'Select',
  data () {
    return {
      
      questionArray:[
          {
            question:"哪个对你来说是美好的下午？",
            picAurl:"./static/1a.jpg",
            picBurl:"./static/1b.jpg",
            bgColor:"#5b67b0",
            textColor:"#ffb400"
          },
          {
            question:"哪个对你来说是难忘的派对？",
            picAurl:"./static/2a.jpg",
            picBurl:"./static/2b.jpg",
            bgColor:"#4ca697",
            textColor:"#ffcc00"
          },
          {
            question:"一个惬意的周末应该怎样度过？",
            picAurl:"./static/3a.jpg",
            picBurl:"./static/3b.jpg",
            bgColor:"#ffd243",
            textColor:"#00b0ff"
          },
          {
            question:"在一个晴朗的冬日，你想去哪里走走？",
            picAurl:"./static/4a.jpg",
            picBurl:"./static/4b.jpg",
            bgColor:"#faa744",
            textColor:"#c10000"            
          },
          {
            question:"你觉得哪个场景里的气氛更危险？",
            picAurl:"./static/5a.jpg",
            picBurl:"./static/5b.jpg",
            bgColor:"#76b447",
            textColor:"#ff8400"        
          },
          {
            question:"你觉得谁更快乐？",
            picAurl:"./static/6a.jpg",
            picBurl:"./static/6b.jpg",
            bgColor:"#0c5eae",
            textColor:"#ffa000"            
          },
          {
            question:"你觉得谁比较能打？",
            picAurl:"./static/7a.jpg",
            picBurl:"./static/7b.jpg",
            bgColor:"#67c3eb",
            textColor:"#ffc300"            
          },
          {
            question:"你觉得谁见到的事情更严重？",
            picAurl:"./static/8a.jpg",
            picBurl:"./static/8b.jpg",
            bgColor:"#cb0b1b",
            textColor:"#00d2ff"            
          }
      ],

      answerArray:[],
      standard:6,
      scoreI: 0,
      scoreS: 0,
      scoreM: 0,
      sectionA: "",
      sectionB: "",
      progress: 1,
      result:{
          id:1,
          name:"鬼畜孩子"
      },
      dots:[]

    }
  },
 
  methods: {
      setValue:function(){
          this.$emit('getdata',this.questionArray)
      },
      next: function(){
          if (this.progress < 8){
            this.progress++;
            console.log("现在是第"+this.progress+"个问题，");
          }else{
              this.$router.push({name:'result',params:{result:this.result}})
              this.getResult();
              alert(this.result.name)
          }
          
      },
      countA: function(){
          var status="";
          switch(this.progress){
            case 1 :
                this.scroeI += 2;
                status = "I+2";
                break;
            
            case 2 :
                this.scoreM += 1;
                status = "M+1";
                break;

            case 3 :
                this.scoreS += 3;
                status = "S+3";
                break;
            
            case 4 :
                this.scoreM += 1;
                status = "M+1";
                break;

            case 5 :
                this.scoreM += 3;
                status = "M+3";
                break;

            case 6 :
                this.scoreI += 2;
                status = "I+2";
                break;

            case 7 :
                this.scoreM += 1;
                status = "M+1";
                break;
            
            case 8 : 
                this.scoreS += 2;
                status = "S+2";
                break;
          }
            //保存选择的数据
            var answers = {};
            answers["questionNo"] = this.progress;
            answers["selection"] = "A";
            answers["status"] = status;
            this.answerArray.push(answers);
            
            console.log("progress:"+this.progress+"--scoreI:"+this.scoreI+"--scoreS:"+this.scoreS+"--scoreM:"+this.scoreM);          
      },
      countB:function(){
          var status="";
          switch(this.progress){
            case 1 :
                this.scroeM += 1;
                status = "M+1";
                break;
            
            case 2 :
                this.scoreI += 2;
                status = "I+2";
                break;

            case 3 :
                this.scoreI += 1;
                status = "I+1";
                break;
            
            case 4 :
                this.scoreS += 3;
                status = "S+3";
                break;

            case 5 :
                this.scoreS += 2;
                status = "S+2";
                break;

            case 6 :
                this.scoreM += 5;
                status = "M+5";
                break;

            case 7 :
                this.scoreI += 1;
                status = "I+1";
                break;
            
            case 8 : 
                this.scoreI += 4;
                status = "I+4";
                break;

          }
            //保存选择的数据
            var answers = {};
            answers["questionNo"] = this.progress;
            answers["selection"] = "A";
            answers["status"] = status;
            this.answerArray.push(answers);
            
            console.log("progress:"+this.progress+"--scoreI:"+this.scoreI+"--scoreS:"+this.scoreS+"--scoreM:"+this.scoreM);
      },
      getResult:function(){
          //1.小孩（）I⾼ S低 M低 / I低 S低 M低  
          //2.少年（）I⾼ S高 M低 / I低 S⾼ M低  
          //3.青年（）I低 S⾼ M高 / I⾼ S低 M⾼
          //4.成年（）I低 / S低 / M⾼  
          //5.王（） I⾼ / S高 / M高;

          if(this.scoreI <= this.standard && this.scoreM <= this.standard && this.scoreS <= this.standard){
              this.result.id = 1;
              this.result.name = "鬼畜孩子";
          }else if(this.scoreI > this.standard && this.scoreM <= this.standard && this.scoreS <= this.standard){
              this.result.id = 1;
              this.result.name = "鬼畜孩子";
          }else if(this.scoreI <= this.standard && this.scoreM > this.standard && this.scoreS <= this.standard){
              this.result.id = 2;
              this.result.name = "肤浅的鬼畜少年";
          }else if(this.scoreI > this.standard && this.scoreM > this.standard && this.scoreS <= this.standard){
              this.result.id = 2;
              this.result.name = "肤浅的鬼畜少年";
          }else if(this.scoreI <= this.standard && this.scoreM > this.standard && this.scoreS > this.standard){
              this.result.id = 3;
              this.result.name = "略有些鬼畜的青年人";
          }else if(this.scoreI > this.standard && this.scoreM <= this.standard && this.scoreS > this.standard){
              this.result.id = 3;
              this.result.name = "略有些鬼畜的青年人";
          }else if(this.scoreI <= this.standard && this.scoreM <= this.standard && this.scoreS > this.standard){
              this.result.id = 4;
              this.result.name = "最不鬼畜的成年人";
          }else if(this.scoreI > this.standard && this.scoreM > this.standard && this.scoreS > this.standard){
              this.result.id = 5;
              this.result.name = "万里挑一的鬼畜之王";
          }
          console.log("test result:"+this.result.name);


      }
  },
  props:["this.result"],
  computed:{
      
  },
  created(){
      console.log("现在是第"+this.progress+"个问题，");
      console.log("progress:"+this.progress+"--scoreI:"+this.scoreI+"--scoreS:"+this.scoreS+"--scoreM:"+this.scoreM);
      this.dots[99]=0;
  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* html{
        background-color: #5b67b0;
        font-size: 64px;
        overflow: auto
    } */
    #Select{
        background-color: #5b67b0;
        max-width: 640px;
        margin: 0 auto;
        padding-top: 1.875rem;
        background-image: url("dot.png");
        background-repeat: repeat;
        overflow: auto;
        padding-bottom: 1.875rem;
    }
    #dots{
        position: absolute;
    }
    .dot{
        width: .125rem;
        height: .125rem;
        background-color: #fff;
        border-radius: 50%;
        margin: .3125rem;
        float: left;
    }
    #question{
        font-size: 1.1875rem;
        padding-bottom: .625rem;
        /* color: #ffb400; */
        text-shadow: 1px 1px 0 #000;
        stroke: .0625rem #000;
        -webkit-stroke: .0625rem #000;
    }
    #progress{
        font-size: 1.1875rem;
        color: #fff;
        font-weight: bolder;
    }
    .section{
        /* background-image: url("dot.png");
        background-repeat: repeat; */
        width: 78%;
        margin: 0 auto;
    }
    .section img{
        width: 100%;
        padding: .3125rem;
        margin-bottom:.625rem;
        background-color: #fff;
        border: .125rem solid #000;
        box-sizing: border-box;
    }

    /* animation */
    .bounce-enter-active {
    animation: bounce-in .5s;
    }
    .bounce-leave-active {
    animation: bounce-in .5s reverse;
    }
    @keyframes bounce-in {
    0% {
        transform: scale(0);
    }
    50% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(1);
    }
    }
</style>



